<template>
  <div id="chart">
    <div id="line" style="width: 90%;height:450px;"></div>
  </div>
</template>

<script>
  let echarts = require('echarts/lib/echarts')
  // 
  require('echarts/lib/chart/pie')
  // title
  require('echarts/lib/component/title')
  require('echarts/lib/component/legend')

  export default {
    name: 'chart',
    props: ['disData'],
    mounted() {
      this.drawLine();
    },
    watch:{
      disData: function (){
        this.drawLine();
      }
    },
    methods: {
      drawLine() {
        // domecharts
        let myChart = echarts.init(document.getElementById('line'))
        // 
        myChart.setOption({
          title : {
            text: '',
            subtext: '',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['','','','','']
          },
          series : [
            {
              name: '',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                {value:this.disData.beijing, name:''},
                {value:this.disData.shanghai, name:''},
                {value:this.disData.shenzhen, name:''},
                {value:this.disData.hangzhou, name:''},
                {value:this.disData.qita, name:''}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });
      }
    }
  }
</script>

<style scoped>

</style>




